* {
    font-family: 'Roboto', sans-serif;
    margin-bottom: 0px;
    box-sizing: border-box !important; }
a {
    text-decoration: inherit;
    display: block; }
li {
    list-style-type: none; }
header {
    position: relative;
    height: 50px; }
nav {
    height: 53px;
    background: #212121;
    border: 1px solid #000000;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    position: fixed;
    width: 100%;
    height: 50px;
    z-index: 2; }
.nav__menu {
    display: flex;
    justify-content: space-between;
    padding: 0;
    padding: 14px 70px; }
.nav__link {
    font-weight: 300;
    font-size: 15px;
    color: #FFFFFF; }
.nav__el {
    border-right: 2px dotted black;
    padding-right: 45px; }
.subheader {
    position: absolute;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, .6);
    padding-top: 17px;
    top: 50px; }
.subheader__logo_link {
    max-width: 100%; }
.subheader__official {
    font-weight: 100;
    font-size: 13px;
    color: #FFFFFF;
    margin-left: 5px; }
.subheader__call-us {
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
    color: #FFFFFF; }
.subheader__tel {
    font-weight: 400;
    font-size: 22px;
    line-height: 44px;
    color: #FFFFFF; }
.subheader__call_btn {
    width: 172px;
    height: 46px;
    border: none;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    font-weight: 400;
    font-size: 14px;
    line-height: 46px;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); }
.promo {
    min-height: 800px;
    background: url(../img/uber_bg.jpg) center center/cover no-repeat; }
h1,
h2 {
    text-align: center;
    color: #FFFFFF; }
.promo__h1 {
    margin-top: 148px;
    font-weight: 700;
    font-size: 60px; }
.promo__h2 {
    font-weight: 700;
    font-size: 36px;
    margin-top: 20px; }
.promo__text {
    margin-top: 22px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF; }
.promo__btn {
    width: 259px;
    height: 63px;
    display: block;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    border: none;
    margin: 115px auto 0; }
.reasons {
    min-height: 767px;
    padding-top: 14px;
    padding-bottom: 56px; }
.label {
    width: 115px;
    height: 27px;
    background: #000000;
    margin: auto;
    font-weight: 400;
    font-size: 11px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    color: #FFFFFF;
    mix-blend-mode: normal; }
.title {
    font-weight: 700;
    font-size: 38px;
    margin-top: 32px;
    text-align: center;
    text-transform: uppercase;
    color: #222222; }
.reasons__block_circle {
    width: 116px;
    height: 116px;
    background: #1EACC7;
    border-radius: 100%;
    align-self: center;
    display: flex;
    justify-content: center;
    align-items: center; }
.reasons__block {
    display: flex;
    justify-content: space-around;
    align-items: start;
    margin-top: 26px; }
.reasons__block_title {
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    color: #1EACC7; }
.reasons__block_text {
    width: 420px;
    font-style: normal;
    font-weight: 300;
    font-size: 17px;
    line-height: 21px; }
.subtitle {
    position: relative;
    margin-top: 19px;
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    text-align: center;
    &_white {
        color: #fff; } }
.label {
	position: relative;
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center;
	&_white {
		background-color: #fff;
		color: #000; } }

.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../img/bg/mobile_bg.png') center (center / cover) no-repeat;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background: url('../img/bg/mobile_screen_1.jpg') center (center / cover) no-repeat; }
        &_2 {
            background: url('../img/bg/mobile_screen_2.png') center (center / cover) no-repeat; }
        &_3 {
            background: url('../img/bg/mobile_screen_3.png') center (center / cover) no-repeat; }
        &_4 {
            background: url('../img/bg/mobile_screen_4.jpg') center (center / cover) no-repeat; }
        &_5 {
            background: url('../img/bg/mobile_screen_5.png') center (center / cover) no-repeat; }
        &_6 {
            background: url('../img/bg/mobile_screen_6.jpg') center (center / cover) no-repeat; }
        &_subtitle {
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            line-height: 31px;
            text-transform: uppercase;
            text-align: center; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            width: 26px;
            height: 26px;
            background-color: #000000;
            margin: 0 auto;
            border-radius: 100%;
            cursor: pointer; } } }
